import React from 'react';
import { Link } from 'react-router-dom';
import { Nav, Navbar } from 'react-bootstrap';
import styled from 'styled-components';


const Styles = styled.div`
  .navbar {
    background-color: #222;
  }

  a, .navbar-brand, .navbar-nav .nav-link {
    color: #bbb;

    &:hover {
      color: white;
    }
  }
`;

function Footer() {
  return (

    <Styles>
  <Navbar expand="lg">
    <Navbar.Brand href="/">  </Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto">
   

    <Nav.Item>
  <Nav.Link>
  <span><Link to="/"> Follow us on Instagram: @gymtrainer2021 </Link></span>
  </Nav.Link>
</Nav.Item>

<Nav.Item>
  <Nav.Link>
  <span><Link to="/"> Facebook: facebook.com/gymtrainer2021 </Link></span>
  </Nav.Link>
</Nav.Item>

<Nav.Item>
  <Nav.Link>
  <span><Link to="/"> Snapchat: @gymtrainer2021 </Link></span>
  </Nav.Link>
</Nav.Item>


<Nav.Item>
  <Nav.Link>
  <span><Link to=" /"> Twitter: @gymtrainer2021 </Link></span>
  </Nav.Link>
</Nav.Item>


<Nav.Item>
  <Nav.Link>
  <span><Link to=" /"> KakaoTalk: @gymtrainer2021 </Link></span>
  </Nav.Link>
</Nav.Item>


</Nav>
    </Navbar.Collapse>
  </Navbar>
</Styles >
  );
}

export default Footer;